import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
        this.state = {
            api: this.api('team/task/people/analysis')(),
            params: {
                "openId": this.cookie.get("openId"),//openId
                "memberOpenId": this.getMemberOpenId(),//openId
                "teamId": this.cookie.get("teamId"),//Id
                "period": "yearly" //(weekly)
                // //weekly14monthly
                // 3031()yearly6
            }
        }
        this.listenables = actions

        this.init()
    }

    init() {

    }

    getMemberOpenId() {
        let pathname = this.$route.getCurrentLocation().pathname
        let params = pathname && pathname.split('/analysis/')[1]
        return params
    }

    onInit(myChart) {
        myChart.showLoading({
            text: '...',    //loading
        });

        this.$ajax.get(this.api('team/task/people/analysis')(), {
            params: this.state.params
        }).then(res => {
            myChart.hideLoading();
            let data = res.data.data
            this.drawChart(myChart, data)
        }).catch(error => {})

    }


    createEmply(num) {
        let arr = []
        for(var i=0; i< num; i++) {
            arr.push('')
        }
        return arr
    }

    drawChart(myChart, data) {
        let type = this.state.params.period
        let xAxisData = []
        if (type === 'yearly') {
            xAxisData = this.createEmply(6)

        } else if (type === 'weekly') {
            //xAxisData = ['', '', '', '', '', '']
            xAxisData = this.createEmply(14)
        } else if (type === 'monthly') {
            xAxisData = this.createEmply(new Date().getDate())
        }

        var option = {
            tooltip: {
                trigger: 'axis'
            },
            legend: {
                data: ['', '', '']
            },
            toolbox: {
                show: true,
                feature: {
                    mark: {show: true},
                    dataView: {show: false, readOnly: false},
                    magicType: {show: false, type: ['line', 'bar', 'stack', 'tiled']},
                    restore: {show: false},
                    saveAsImage: {show: false}
                }
            },
            calculable: true,
            xAxis: [
                {
                    type: 'category',
                    boundaryGap: false,
                    data: xAxisData
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            series: [
                {
                    name: '',
                    type: 'line',
                    stack: '',
                    data: data['overTime']
                },
                {
                    name: '',
                    type: 'line',
                    stack: '',
                    data: data['unfinished']
                },
                {
                    name: '',
                    type: 'line',
                    stack: '',
                    data: data['finished']
                }
            ]
        };

        myChart.setOption(option);
        //myChart.dispose();
    }

    onRemoveEvents() {
        this.clearEvents()
    }
}

export default Store



