import RootStore from 'core/Store'
import * as api from 'core/api'

class Store extends RootStore {
    constructor(actions) {
        super();
	    this.state = {
		   /* list: [
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    },
			    {
				    listTitle: {
					    titleDate:'2016.01.01',
					    titleCon:'WECHATE | UI'
				    },
				    listCon:''
			    }
		    ],*/
		    api: this.api('memo/memorabilia/detail')(),
		    params: {
			    "currentPageNumber": 1,
			    "pageSize": 10,
			    "subprojectId": this.getId() || 256,
			    "searchString":""
		    }
	    }
        this.listenables = actions

        this.init()
    }

	getId() {
		let pathname = this.$route.getCurrentLocation().pathname
		let id = pathname && pathname.split('/memorabilia/')[1]
		return id ? id : ''
	}

	onDidMount() {
		this.$emit('components/services/MemoSteps', {
			api: this.state.api,
			params: this.state.params,
			type: 'memorabiliaDetail'
		})

		this.$emit('components/services/RoundSearch/type', {
			type: 'memorabiliaDetail'
		})
	}

    init() {
	    this.$on('components/services/RoundSearch/memorabiliaDetail', (key, { value }) => {
		    console.log('components/services/RoundSearch/memorabiliaDetail', value)
		    this.setState({
			    params: Object.assign({}, this.state.params, {
				    "currentPageNumber": 1,
				    "searchString": value
			    })
		    })
		    let timer = setTimeout(() => {
			    this.$emit('components/services/MemoSteps', {
				    api: this.state.api,
				    params: this.state.params,
				    type: 'memorabiliaDetail'
			    })
			    clearTimeout(timer)
			    //console.log('this.state.params', this.state.params)
		    }, 0)
	    })
    }

    onRemoveEvents() {
        this.clearEvents()
    }

}

export default Store



