import React, { PropTypes } from 'react'
import Reflux from 'reflux'
import Store from './store'
import Actions from './actions'
import './styles'

import ItemThumb from 'widgets/ItemThumb'
import ItemButton from 'widgets/ItemButton'
import PullUp from 'widgets/PullUp'

class ProjectListPage extends React.Component {
    constructor(props) {
        super(props)
        this.actions = new Actions()
        this.store = new Store(this.actions)
        this.state = this.store.getState()
        this.store.listen((...data) => {
            this.setState(...data)
        })

        this.stageColor = {
            "red": "#cf4b55",
            "blue": "#6da0eb",
            "violet": "#176ceb",
            "yellow": "#fcff04",
            "purple": "#c740fc",
            "green": "#41d119",
            "grey": "#666"
        }
        //,)
        /*
        #c740fc
        #f85f57
        #176ceb
        #41d119
        #02b0f9
        #71757b
        #fcff04*/
        this.stageColor = {
            "": "#c740fc",
            "": "#f85f57",
            "": "#176ceb",
            "": "#41d119",
            "": "#02b0f9",
            "": "#71757b",
            "": "#fcff04"
        }
        /*this.stageList = {
            a: "",
            b: "",
            c: "",
            d: ""
        }*/
    }

    componentDidMount() {

    }

    componentWillUnmount() {
        // no del
        this.actions.removeEvents()
    }

    getUserTs(userTs) {
        if (userTs.length === 0) return
        let usersName = []
        for (var i = 0; i < userTs.length; i++) {
            usersName.push(userTs[i].userName)
        }
        return usersName.join(',')
    }

    renderItem = (item, index) => {
        return (
            <ItemThumb
                key={ index }
                onEdit={ () => {this.actions.handleEdit( item )} }
                onDetail={ ()=> {this.actions.handleShowTask(item.projectId, item.subprjectId)}}
                thumb={
                        <div style={{
                        'height':'100%',
                        'backgroundColor': this.stageColor[item.teamStatus]
                        }}></div>
                    }
                title={ `${item.project} | ${item.subproject}`  }
                desc={ `:${this.getUserTs(item.userTs)}, :${ item.teamStatus }` }
            />
        )
    }

    render() {
        return (
            <div className="root-page-projectlist">
                <PullUp
                    dataField="projects"
                    api={ this.state.api }
                    params={ this.state.params }
                    renderItem={ this.renderItem }
                >
                    <ItemButton label="" onDetail={ () => { this.actions.add()  } }/>
                </PullUp>
            </div>
        )
    }

}

export default ProjectListPage



